@import url('https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap');

.home {
    width: 100%;
    height: 100vh;
    padding: 0;
    margin: 0;
}
.background {
    z-index: 1;
    width: 100%;
    height: 100vh;
    background-image: url('../../assets/Wave.webp');
    background-size: cover;
    background-repeat: no-repeat;
}
.blur {
    background: rgba(0, 0, 0, 0.2);
    width: 100%;
    height: 100vh;
    backdrop-filter: blur(0px);
    z-index: 10;
    position: absolute;
}
.left {
    width: 100%;
    height: 100vh;
    top: 0%;
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: white;
    text-align: center;
    z-index: 100;
}
.left div {
    width: 50%;
    margin-bottom: 5%;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.left h1 {
    font-size: 4vw;
    font-family: "Roboto", serif;
    font-weight: 300;
    font-style: normal;
    width: 150%;
}
.left p {
    width: 85%;
    font-family: "Roboto", serif;
    font-weight: 300;
    font-style: normal;
    opacity: 90%;
}
.left button {
    width: 13%;
    height: 5.5vh;
    background: rgba(255, 255, 255, 0.155);
    backdrop-filter: blur(5px);
    border: none;
    cursor: pointer;
    border-radius: 10px;
    color: #d6d6d6;
    box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.1);
}
html[data-lang="ge"] .left h1 {
    font-size: 3.5vw;
}
::-webkit-scrollbar {
    width: 9px; /* Width of the scrollbar */
    height: 12px; /* Height of the scrollbar for horizontal scroll */
}

/* Track */
::-webkit-scrollbar-track {
    background: #f0f0f0; /* Background color of the scrollbar track */
    border-radius: 10px; /* Optional: round the corners */
}

/* Thumb */
::-webkit-scrollbar-thumb {
    background-color: #264653; /* Color of the scrollbar thumb */
    border-radius: 10px; /* Round the corners of the thumb */
    border: 3px solid #f0f0f0; /* Adds space around thumb */
}
@media (max-width: 480px) {
    .background {
        width: 100%;
    }
    .blur {
        backdrop-filter: blur(0px);
        height: 102vh;
        display: none;
    }
    .left h1 {
        font-size: 7vw;
        width: 100%;
    }
    html[data-lang="ge"] .left h1 {
        font-size: 6vw;
    }
    .left div {
        width: 100%;
        margin-bottom: 25%;
    }
    .left h2 {
        font-size: 9vw;
        font-family: "Roboto", serif;
        font-weight: 300;
        font-style: normal;
        position: relative;
        bottom: 15%;
    }
    .left p {
        font-size: 5vw;
        color: #dadada;
    }
    html[data-lang="ge"] .left p {
        font-size: 4vw;
    }
    .left button {
        width: 30%;
        height: 4vh;
        background: none;
        border: 1px solid #bfbfbf;
        background: #d9d9d929;
        border-radius: 10px;
        color: rgba(255, 255, 255, 0.895);
        backdrop-filter: blur(2px);
        font-family: "Roboto", serif;
        font-weight: 300;
        font-style: normal;
    }
}
